body{
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(221,217,231,1) 73%, rgba(0,212,255,1) 100%);;
}
.app {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 40px 24px;
 background: rgb(2,0,36);
 position:100;
 height: 100%;
 background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(221,217,231,1) 73%, rgba(0,212,255,1) 100%);
}

.app__searchForm {
  margin-top: 18px;
  border-radius: 10px;
}

.app__input {
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 15px;
}

.app__submit {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: #02aff0;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.app__submit:hover {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: white;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: #02aff0;
  cursor: pointer;
}

.app__recipes {
  margin-top: 24px;
  display: grid;
  grid-template-columns: 220px 220px;
  align-items: baseline;
}
.app_healthLable{
  padding: 7px;
  margin-left:6px ;
  border-radius: 4px;
}
